.Preloader_ldsDualRing__2ccPD {
    display: inline-block;
    width: 80px;
    height: 80px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}

.Preloader_ldsDualRing__2ccPD:after {
    content: " ";
    display: block;
    width: 64px;
    height: 64px;
    margin: 0;
    border-radius: 50%;
    border: 6px solid rgb(19, 1, 1);
    border-color: rgb(17, 1, 1) transparent rgb(10, 1, 1) transparent;
    -webkit-animation: Preloader_ldsDualRing__2ccPD 1.2s linear infinite;
            animation: Preloader_ldsDualRing__2ccPD 1.2s linear infinite;
}

@-webkit-keyframes Preloader_ldsDualRing__2ccPD {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
}

@keyframes Preloader_ldsDualRing__2ccPD {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
}
